.numpurchases {
  margin-top: 260px; }

.dashboard-h2 {
  font-weight: bold;
  font-size: 36px;
  line-height: 49px;
  letter-spacing: 0.015em;
  color: #2B3F64; }

.numpurchases-block {
  margin-top: 80px;
  padding: 76px 89px 85px;
  box-sizing: border-box;
  background: #3B4255;
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.38);
  border-radius: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.numpurchases-info {
  width: 378px; }

@keyframes w100anim {
  0% {
    width: 0; }
  100% {
    width: 100%; } }

.numpurchases-info-item {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #ECECEC;
  padding-bottom: 7px;
  color: #D0D0D0;
  position: relative;
  cursor: pointer;
  /*&.numpurchases-info-item--active{
		pointer-events: none;
	}*/
  transition-duration: .3s; }
  .numpurchases-info-item:not(:hover) {
    color: #D0D0D0 !important; }
  .numpurchases-info-item + .numpurchases-info-item {
    margin-top: 7px; }
  .numpurchases-info-item__name {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 150%;
    /* or 27px */ }
  .numpurchases-info-item__value {
    font-weight: 600;
    font-size: 18px;
    line-height: 150%;
    /* or 27px */
    color: #FFFFFF;
    min-width: 75px;
    margin-left: 10px;
    text-align: right; }
  .numpurchases-info-item__progess {
    position: absolute;
    bottom: -1px;
    left: 0;
    height: 1px;
    transition-duration: .5s; }
    .numpurchases-info-item__progess--anim {
      animation: w100anim 3s linear forwards; }

.numpurchases-chart-wrap {
  width: 643px;
  position: relative; }
  .numpurchases-chart-wrap::before {
    content: '';
    display: block;
    padding-top: 100%; }

#numpurchases-chart {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  left: -3px;
  top: -3px;
  width: calc(100% + 6px);
  height: calc(100% + 6px);
  cursor: pointer;
  border-radius: 50%;
  overflow: hidden; }

#numpurchases-chart-active-level {
  position: absolute;
  left: -15px;
  top: -15px;
  width: calc(100% + 30px);
  height: calc(100% + 30px);
  z-index: 2;
  pointer-events: none;
  opacity: .7; }

